<template>
  <div :class="`${cls}`">
    <div :class="`${cls}__box`">
      <button :class="`${cls}__closed`" @click="hideForm()">закрыть</button>
      <!--Список полей-->
      <div :class="`${cls}__list`">
        <div :class="`${cls}__item`" v-for="(item, key) in fields" :key="key">
          <label :class="`${cls}__label`">{{ key }}</label>
          <input
            :class="`${cls}__field`"
            :type="fieldsType[key] ? fieldsType[key] : ''"
            v-model.trim="fields[key]"
          />
        </div>
      </div>
      <!--Ошибка-->
      <div v-if="error" :class="`${cls}__error error`">{{ error }}</div>
      <!--Кнопка добавить-->
      <button :class="`${cls}__btn-add`" @click="addProduct()">Добавить товар в список</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "form-product-add",
  data() {
    return {
      cls: "form-product-add",
      // Типы полей отличные от text (для input)
      fieldsType: {
        "Количество на складе": "number",
        Цена: "number",
      },
      // Поля формы
      fields: {
        "Название товара": "",
        Артикул: "",
        Производитель: "",
        "Количество на складе": "",
        Цена: "",
      },
      // Ошибка при заполнении
      error: "",
    };
  },
  methods: {
    // Проверка полей
    checkFieldsError() {
      let isError = false;
      for (let item in this.fields) {
        if (!this.fields[item]) {
          isError = true;
        }
      }
      return isError;
    },
    // Добавить товар в список
    addProduct() {
      this.error = "";
      if (this.checkFieldsError()) {
        this.error = "Заполните все поля";
        return;
      }
      this.$store.commit("products/addProduct", this.fields);
      this.clearForm();
    },
    // Очищаем форму
    clearForm() {
      for (let item in this.fields) {
        this.fields[item] = "";
      }
    },
    // Создаем событие чтобы скрыть форму
    hideForm() {
      this.$emit("hide");
    },
  },
};
</script>

