.form-product-add
  position: fixed
  top: 0
  left: 0
  right: 0
  bottom: 0
  z-index: 10
  display: flex
  justify-content: center
  align-items: center
  background-color: rgba(0, 0, 0, .5)
  &__box
    padding: 20px
    background-color: #fff
    position: relative
  &__closed
    cursor: pointer
    position: absolute
    right: 0
    top: 0
  &__label
    display: block
  &__btn-add
    margin-top: 10px
</style>
