<template>
  <div :class="`${cls}  ${cls}--${isDisplayTilesCatalogList ? 'tile' : 'line'}`">
    <!--Список товаров-->
    <div :class="`${cls}__list`">
      <div :class="`${cls}__item`" v-for="product in productItems" :key="product.id">
        <div
          :class="`${cls}__value`"
          v-for="(productValue, productKey) in product.fields"
          :key="productKey"
        >
          <b>{{ productKey }}:</b> {{ productValue }}
        </div>

        <div :class="`${cls}__bottom-panel`" :data-catalog-list-bottom-panel="product.id">
          <router-link :class="`${cls}__link`" :to="`/product/${product.id}`"
            >Просмотреть товар</router-link
          >
          <button :class="`${cls}__btn-delete`" @click="productIdDelete = product.id">
            Удалить
          </button>
        </div>
      </div>
    </div>
    <!--Попап подтвердить удаление-->
    <popup
      v-if="productIdDelete"
      title="Вы уверены что хотите удалить этот товар?"
      btnCancel="Отмена"
      btnConfirm="Удалить"
      @action="processingConfirmDelete($event)"
    />
  </div>
</template>

<script>
import Popup from "@/components/popup/popup.vue";

export default {
  name: "catalog-list",
  components: {
    Popup,
  },
  data() {
    return {
      cls: "catalog-list",
      // Текущий id товара для удаления
      productIdDelete: null,
    };
  },
  computed: {
    // Список товаров
    productItems() {
      return this.$store.state.products.items;
    },
  },
  props: {
    isDisplayTilesCatalogList: {
      type: Boolean,
      default: true,
    },
  },
  methods: {
    // Удалить товар
    deleteProduct(productId) {
      this.$store.commit("products/deleteProduct", productId);
    },
    // Обработка действия в Popup который запрашивает подтверждение на удаление
    processingConfirmDelete(event) {
      if (event == "confirm") {
        this.deleteProduct(this.productIdDelete);
      }
      this.productIdDelete = null;
    },
  },
};
</script>

