.catalog-list
  &__list
    display: flex
    flex-wrap: wrap
  &__item
    border: 1px solid #5F9EA0
    padding: 10px
  &__value
    padding: 5px 0
  &__bottom-panel
    display: flex
    justify-content: space-between
    position: relative
  &--tile
  .catalog-list
    &__item
      margin: 3px
      width: calc((100% / 3) - 6px)
  &--line
    .catalog-list
      &__item
        width: 100%
        margin-bottom: 10px
</style>
