<template>
  <div class="page page--catalog">
    <h1>Список товаров</h1>
    <form-product-add v-if="isShowFormAdd" @hide="isShowFormAdd = false" />
    <button @click="isShowFormAdd = true">Добавить товар</button>
    <button @click="changeDisplayCatalogList()">
      Отобразить {{ isDisplayTilesCatalogList ? "списком" : "плиткой" }}
    </button>
    <catalog-list :isDisplayTilesCatalogList="isDisplayTilesCatalogList" />
  </div>
</template>

<script>
import CatalogList from "./catalog-list.vue";
import FormProductAdd from "./form-product-add.vue";
export default {
  name: "page-catalog",
  components: {
    CatalogList,
    FormProductAdd,
  },
  data() {
    return {
      // Показать / скрыть форму добавления товара
      isShowFormAdd: false,
    };
  },
  computed: {
    // Отображение списка товаров плиткой или в линию
    isDisplayTilesCatalogList() {
      return this.$store.state.catalogList.isDisplayTilesCatalogList;
    },
  },
  methods: {
    // Переключаем отображение списка товаров - плитка / линия
    changeDisplayCatalogList() {
      let isDisplayTiles = !this.isDisplayTilesCatalogList;
      this.$store.commit("catalogList/changeDisplayCatalogList", isDisplayTiles);
    },
  },
};
</script>
<style lang="sass">
.page
  &--catalog
    button
      margin: 5px
</style>
