<template>
  <div :class="`${cls}`">
    <div :class="`${cls}__box`">
      <div v-if="title" :class="`${cls}__title`">{{ title }}</div>
      <div :class="`${cls}__bottom-panel`">
        <button v-if="btnCancel" :class="`${cls}__btn-cancel`" @click="emit('cancel')">
          {{ btnCancel }}
        </button>
        <button v-if="btnConfirm" :class="`${cls}__btn-action`" @click="emit('confirm')">
          {{ btnConfirm }}
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "popup",
  data() {
    return {
      cls: "popup",
    };
  },
  props: {
    // Заголовок
    title: {
      type: String,
      default: "",
    },
    // Текст кнопки Отмена
    btnCancel: {
      type: String,
      default: "",
    },
    // Текст кнопки Подтверждения действия
    btnConfirm: {
      type: String,
      default: "",
    },
  },
  methods: {
    // Создать событие Отмена / Подтверждения действия (по параметру typeAction можно будет определить какую кнопку нажал пользователь)
    emit(typeAction) {
      this.$emit("action", typeAction);
    },
  },
};
</script>

<style scoped lang="sass">
.popup
  position: fixed
  top: 0
  left: 0
  right: 0
  bottom: 0
  z-index: 10
  display: flex
  justify-content: center
  align-items: center
  background-color: rgba(0, 0, 0, .5)
  &__box
    background-color: #fff
    border: 1px solid #5F9EA0
    padding: 10px
    width: 300px
  &__title
    margin-bottom: 10px
  &__bottom-panel
    display: flex
    justify-content: space-between
</style>
